@mixin flex($justifyContent, $flexDirection, $alignItems) {
  display: flex;
  justify-content: $justifyContent;
  flex-direction: $flexDirection;
  align-items: $alignItems; }

.componentsExchangeSellBalance {
  font-size: 16px;
  font-weight: bold;
  font-family: "Roboto",sans-serif;
  color: #E48900;
  margin: 0;
  padding: 0; }

.componentsExchangeSellInfoRow {
  @include flex(start, row, center);
  margin: 0 0 5px 0; }

.componentsExchangeSellBuyVIP {
  @include flex(center, row, center);
  margin-top: 10px; }

.componentsExchangeSellFooter {
  @include flex(center, column, center);
  margin-top: 10px; }
